import misEnum from '../../common/mis-enum.js';
export default {
	data() {
		return {
			StatusEnumMap: [],
			steps: [],
			color: '',
			info: {},
			id: 0,
			showModal: false,
			cancelReason: "",
			require: "无"
		}
	},
	onLoad(option) {
		this.id = option.id;
	},
	onShow() {
		this.loadData();
	},
	methods: {
		showSteps() {
			//设置进度步骤
			this.steps.length = 0;
			switch (this.info.status) {
				case 0:
					this.steps.push({
						name: this.StatusEnumMap.get(0),
						color: 'text-green'
					});
					this.steps.push({
						name: "审批",
						color: ''
					});
					this.steps.push({
						name: "调度",
						color: ''
					});
					this.steps.push({
						name: "订单进行中",
						color: ''
					});
					this.steps.push({
						name: "订单完成",
						color: ''
					});
					break;
				case 1:
					this.steps.push({
						name: this.StatusEnumMap.get(0),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(1),
						color: 'text-green'
					});
					this.steps.push({
						name: "调度",
						color: ''
					});
					this.steps.push({
						name: "订单进行中",
						color: ''
					});
					this.steps.push({
						name: "订单完成",
						color: ''
					});
					break;
				case -1:
					this.steps.push({
						name: this.StatusEnumMap.get(0),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(-1),
						color: 'text-red'
					});
					this.steps.push({
						name: "调度",
						color: ''
					});
					this.steps.push({
						name: "订单进行中",
						color: ''
					});
					this.steps.push({
						name: "订单完成",
						color: ''
					});
					break;
				case 2:
					this.steps.push({
						name: this.StatusEnumMap.get(0),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(1),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(2),
						color: 'text-green'
					});
					this.steps.push({
						name: "订单进行中",
						color: ''
					});
					this.steps.push({
						name: "订单完成",
						color: ''
					});
					break;
				case -2:
					this.steps.push({
						name: this.StatusEnumMap.get(0),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(1),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(-2),
						color: 'text-red'
					});
					this.steps.push({
						name: "订单进行中",
						color: ''
					});
					this.steps.push({
						name: "订单完成",
						color: ''
					});
					break;
				case 3:
					this.steps.push({
						name: this.StatusEnumMap.get(0),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(1),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(2),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(3),
						color: 'text-green'
					});
					this.steps.push({
						name: "订单完成",
						color: ''
					});
					break;
				case -3:
					this.steps.push({
						name: this.StatusEnumMap.get(0),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(-3),
						color: 'text-red'
					});
					break;
				case 4:
					this.steps.push({
						name: this.StatusEnumMap.get(0),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(1),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(2),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(3),
						color: 'text-green'
					});
					this.steps.push({
						name: this.StatusEnumMap.get(4),
						color: 'text-green'
					});
					break;
			}
		},
		loadData() {
			global.$http.post('/car/apply/getInfo', {
				params: {
					apply_id: this.id
				},
			}).then(res => {
				if (res.status === "0") {
					this.info = res.data;
					this.StatusEnumMap = misEnum.UseCarRecordEnumMap;
					this.showSteps();
					//设置用车要求
					this.info.nums.forEach(c=>{
						if(c.num > 0)
						{
							if(this.require == "无")
							{
								this.require = c.car_type+c.num+"辆";
							}else{
								this.require += "; " + c.car_type+c.num+"辆";
							}
						}
					});
				} else {
					uni.showToast({
						title: res.msg,
						icon: 'none'
					});
				}
			}).catch(err => {
				uni.showToast({
					title: err.message,
					icon: 'none'
				});
			});
		},
		cancleBook: function(){
			this.showModal = true;
		},
		hideModal: function(){
			this.showModal = false;
		},
		fillCancelReason: function(e){
			this.cancelReason = e.detail.value;
		},
		sureModal: function() {
			if(this.cancelReason.length <= 0){
				uni.showToast({
					title: "请填写取消原因",
					icon: 'none'
				});
				return;
			}
			this.showModal = false;
			uni.showLoading({
				title: '提交中',
				mask: false
			});
			global.$http.post('/car/apply/cancel', {
				params: {
					apply_id: this.id,
					cancel_reason: this.cancelReason
				},
			}).then(res => {
				uni.hideLoading();
				if (res.status === "0") {
					this.info.status = -2;
					this.info.cancel_reason = this.cancelReason;
					uni.showToast({
						title: "取消成功",
						icon: 'none'
					});
					this.showSteps();
				} else {
					uni.showToast({
						title: res.msg,
						icon: 'none'
					});
				}
			}).catch(err => {
				uni.hideLoading();
				uni.showToast({
					title: err.message,
					icon: 'none'
				});
			});
		},
		toDispatch: function(e) {
			if (this.info.status == 1 || this.info.status == 2) {
				uni.navigateTo({
					url: '../work/dispatchUseCar?para=' + encodeURIComponent(JSON.stringify(this.info))
				});
			} else {
				uni.navigateTo({
					url: '../work/dispatchUseCarDetail?para=' + encodeURIComponent(JSON.stringify(this.info))
				});
			}
		}
	}
}
